import { CellOutRange } from './CellOutRange';
import { XLineIteratorFilter } from '../../XLineIteratorFilter';
import { PlainUtils } from '../../../../../utils/PlainUtils';
import { BaseFont } from '../../../../../canvas/font/BaseFont';
import { ColsIterator } from '../../../iterator/ColsIterator';

class RCellOutRange extends CellOutRange {

  run({
    row, col,
  }) {
    return this.master({ row, col }) && this.right({ row, col }) && this.left({ row, col })
      ? XLineIteratorFilter.RETURN_TYPE.EXEC
      : XLineIteratorFilter.RETURN_TYPE.JUMP;
  }

  master({
    row, col,
  }) {
    const { table } = this;
    const { cells, cols } = table;
    const next = cells.getCell(row, col + 1);
    const master = cells.getCell(row, col);

    // 
    if (PlainUtils.isUnDef(master) || PlainUtils.isBlank(master.text)) {
      return true;
    }
    // 
    const { fontAttr } = master;
    const { direction } = fontAttr;
    // 
    if (direction === BaseFont.TEXT_DIRECTION.ANGLE) {
      const { angle } = fontAttr;
      if (table.isAngleBarCell(row, col)) {
        return angle < 0;
      }
      if (angle === 90 || angle === -90) {
        return true;
      }
    } else {
      const { textWrap } = fontAttr;
      if (textWrap !== BaseFont.TEXT_WRAP.OVER_FLOW) {
        return true;
      }
    }
    // 
    const { align } = fontAttr;
    if (align !== BaseFont.ALIGN.left && align !== BaseFont.ALIGN.center) {
      return true;
    }
    // 
    const width = table.getCellContentBoundOutWidth(row, col);
    const maxWidth = cols.getWidth(col);
    if (width > maxWidth) {
      // next
      // 
      if (direction === BaseFont.TEXT_DIRECTION.ANGLE
        || PlainUtils.isUnDef(next) || PlainUtils.isBlank(next.text)) {
        return false;
      }
    }
    return true;
  }

  left({
    row, col,
  }) {
    const { table } = this;
    const { cells, cols, merges } = table;
    const next = cells.getCell(row, col + 1);
    const master = cells.getCell(row, col);
    let find = true;
    let leftWidth = cols.getWidth(col - 1) + cols.getWidth(col);

    // 
    if (table.hasAngleCell(row)) {
      ColsIterator.getInstance()
        .setBegin(col - 1)
        .setEnd(0)
        .setLoop((i) => {
          // 
          const cell = cells.getCell(row, i);
          if (PlainUtils.isUnDef(cell)) {
            return true;
          }
          // 
          const merge = merges.getFirstIncludes(row, i);
          if (PlainUtils.isNotUnDef(merge)) {
            return true;
          }
          // 
          const { text } = cell;
          if (PlainUtils.isBlank(text)) {
            return true;
          }
          // 
          const { fontAttr } = cell;
          const { direction } = fontAttr;
          // 
          if (direction === BaseFont.TEXT_DIRECTION.ANGLE) {
            const { angle } = fontAttr;
            // angleBar
            if (table.isAngleBarCell(row, i)) {
              if (angle < 0) {
                return true;
              }
            } else {
              // 90
              if (angle === 90 || angle === -90) {
                return true;
              }
              // 
              const { align } = fontAttr;
              if (align !== BaseFont.ALIGN.left && align !== BaseFont.ALIGN.center) {
                return true;
              }
            }
          } else {
            // 
            const { textWrap } = fontAttr;
            if (textWrap !== BaseFont.TEXT_WRAP.OVER_FLOW) {
              return true;
            }
            // 
            const { align } = fontAttr;
            if (align !== BaseFont.ALIGN.left && align !== BaseFont.ALIGN.center) {
              return true;
            }
          }
          // 
          const width = table.getCellContentBoundOutWidth(row, i);
          if (width > leftWidth) {
            // master
            // next
            // 
            const masterBlank = PlainUtils.isUnDef(master) || PlainUtils.isBlank(master.text);
            const nextBlank = PlainUtils.isUnDef(next) || PlainUtils.isBlank(next.text);
            if (direction === BaseFont.TEXT_DIRECTION.ANGLE
              || (masterBlank && nextBlank)) {
              find = false;
            }
          }
          return false;
        })
        .setNext((i) => {
          leftWidth += cols.getWidth(i);
        })
        .execute();
    } else {
      ColsIterator.getInstance()
        .setBegin(col - 1)
        .setEnd(0)
        .setLoop((i) => {
          // 
          const cell = cells.getCell(row, i);
          if (PlainUtils.isUnDef(cell)) {
            return true;
          }
          // 
          const merge = merges.getFirstIncludes(row, i);
          if (PlainUtils.isNotUnDef(merge)) {
            return true;
          }
          // 
          const { text } = cell;
          if (PlainUtils.isBlank(text)) {
            return true;
          }
          // 
          const { fontAttr } = cell;
          const { textWrap } = fontAttr;
          // 
          if (textWrap !== BaseFont.TEXT_WRAP.OVER_FLOW) {
            return false;
          }
          // 
          const { align } = fontAttr;
          if (align !== BaseFont.ALIGN.left && align !== BaseFont.ALIGN.center) {
            return false;
          }
          // 
          const width = table.getCellContentBoundOutWidth(row, i);
          // 
          if (width > leftWidth) {
            // master
            // next
            // 
            const masterBlank = PlainUtils.isUnDef(master) || PlainUtils.isBlank(master.text);
            const nextBlank = PlainUtils.isUnDef(next) || PlainUtils.isBlank(next.text);
            if (masterBlank && nextBlank) {
              find = false;
            }
          }
          return false;
        })
        .setNext((i) => {
          leftWidth += cols.getWidth(i);
        })
        .execute();
    }
    return find;
  }

  right({
    row, col,
  }) {
    const { table } = this;
    const { cells, cols, merges } = table;
    const { len } = cols;
    const master = cells.getCell(row, col);
    let find = true;
    let rightWidth = cols.getWidth(col + 1);

    // 
    if (table.hasAngleCell(row)) {
      ColsIterator.getInstance()
        .setBegin(col + 1)
        .setEnd(len)
        .setLoop((j) => {
          // 
          const cell = cells.getCell(row, j);
          if (PlainUtils.isUnDef(cell)) {
            return true;
          }
          // 
          const merge = merges.getFirstIncludes(row, j);
          if (PlainUtils.isNotUnDef(merge)) {
            return true;
          }
          // 
          const { text } = cell;
          if (PlainUtils.isBlank(text)) {
            return true;
          }
          // 
          const { fontAttr } = cell;
          const { direction } = fontAttr;
          // 
          if (direction === BaseFont.TEXT_DIRECTION.ANGLE) {
            const { angle } = fontAttr;
            // angleBar
            if (table.isAngleBarCell(row, j)) {
              if (angle > 0) {
                return true;
              }
            } else {
              // 90
              if (angle === 90 || angle === -90) {
                return true;
              }
              // 
              const { align } = fontAttr;
              if (align !== BaseFont.ALIGN.right && align !== BaseFont.ALIGN.center) {
                return true;
              }
            }
          } else {
            // 
            const { textWrap } = fontAttr;
            if (textWrap !== BaseFont.TEXT_WRAP.OVER_FLOW) {
              return true;
            }
            // 
            const { align } = fontAttr;
            if (align !== BaseFont.ALIGN.right && align !== BaseFont.ALIGN.center) {
              return true;
            }
          }
          // 
          const width = table.getCellContentBoundOutWidth(row, j);
          if (width > rightWidth) {
            // master
            // 
            if (PlainUtils.isUnDef(master) || PlainUtils.isBlank(master.text)) {
              find = false;
              return false;
            }
          }
          return true;
        })
        .setNext((j) => {
          rightWidth += cols.getWidth(j);
        })
        .execute();
    } else {
      ColsIterator.getInstance()
        .setBegin(col + 1)
        .setEnd(len)
        .setLoop((j) => {
          // 
          const cell = cells.getCell(row, j);
          if (PlainUtils.isUnDef(cell)) {
            return true;
          }
          // 
          const merge = merges.getFirstIncludes(row, j);
          if (PlainUtils.isNotUnDef(merge)) {
            return true;
          }
          // 
          const { text } = cell;
          if (PlainUtils.isBlank(text)) {
            return true;
          }
          // 
          const { fontAttr } = cell;
          const { textWrap } = fontAttr;
          // 
          if (textWrap !== BaseFont.TEXT_WRAP.OVER_FLOW) {
            return false;
          }
          // 
          const { align } = fontAttr;
          if (align !== BaseFont.ALIGN.right && align !== BaseFont.ALIGN.center) {
            return false;
          }
          // 
          const width = table.getCellContentBoundOutWidth(row, j);
          if (width > rightWidth) {
            // master
            // 
            if (PlainUtils.isUnDef(master) || PlainUtils.isBlank(master.text)) {
              find = false;
            }
          }
          return false;
        })
        .setNext((j) => {
          rightWidth += cols.getWidth(j);
        })
        .execute();
    }
    return find;
  }


}

export {
  RCellOutRange,
};
